.name {
	width: 320px;
}
.orderWrapper {
	max-height: 452px;
	overflow-x: hidden;
}
.orderWrapper::-webkit-scrollbar {
	background-color: #2F2F37;
	width: 10px;
	height: 8px;
}
.orderWrapper::-webkit-scrollbar-thumb {
	background: #8585AD;
}

.order, .footer {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.orderInfo {
	width: 640px;
	margin: 0 auto;
	text-align: left;
}
.orderHeader {
	margin-top: -45px;
}
.imageWrapper {
	width: 64px;
	height: 64px;
	border-radius: 100px;
	position: relative;
	overflow: hidden;
	border: solid 2px #4C4CFF;
	padding: 2px;
	box-sizing: border-box;
}
.image {
	position: absolute;
	z-index: 1;
	left: 0;
	top: 0;
	height: 100%;
	object-fit: cover;
	object-position: -30px 0;
	background: #1C1C21;
}
.info, .price {
	display: flex;
	align-items: center;
}
